<template>
  <div class="el-container">

    <!-- 首部banner -->
    <v-Header />

    <!-- 注册框 -->
    <section v-lazy:background-image="`https://assets.vmei.com/system/cosmos/images/newCosmos/news_banner.jpg`" class="section-one">
      <div class="mid">
        <p class="low-params">VMEI BEAUTY NEWS</p>
        <p class="high-params">新闻中心</p>
      </div>
    </section>

    <!-- icon -->
    <section class="section-two">
      <div class="container">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-home"></use>
        </svg>

        <router-link :to="`/news/`">
          企业动态
        </router-link>
      </div>
    </section>
    <!-- icon -->
    <transition name="fade">
      <router-view />
    </transition>

    <v-Footer />

  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      currentActivice: 0
    }
  }
}
</script>

<style lang="scss" scoped>
.el-container {
  width: 100%;
  background-color: #fff;
  flex-direction: column;

  .section-one {
    color: #fff;
    height: 500px;
    flex-direction: column;
    justify-content: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    .mid {
      background: rgba(0,0,0,0.2);
      color: #fff;
      text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center;
      line-height: 4.5em;
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: center;

      p.low-params {
        font-size: 44px;
        font-weight: 100;
        font-family: sans-serif;
      }
      p.high-params {
        font-size: 50px;
        font-weight: 600;
        letter-spacing: 10px;
      }
    }
  }
  .section-two {
    height: 58px;
    border-bottom: 1px solid #eee;
    background-color: #fff;
    width: 100%;

    .container {
      margin: auto;
      border-left: 1px solid #eee;
      width: 100%;
      height: 100%;
      max-width: 1200px;
      font-size: 20px;
      display: flex;
      align-items: center;

      .icon {
        font-size: 30px;
        margin: 20px;
      }
    }
  }
}

@media (max-width: 600px) {
  .el-container {
    overflow: hidden;
    .section-one {
      margin-top: 0;
      padding: 0;
      height: 540px;
    }
    .section-two {
    }
  }
}


</style>



