// package
import Vue from 'vue'
import VueLazyload from 'vue-lazyload'
// import 'babel-polyfill'
import Nprogress from 'nprogress'
import 'nprogress/nprogress.css'

// local file && component
import App from './App'
import router from './router'
import store from './store'
import Header from './components/Header'
import Hr from './components/hr'
import Footer from './components/Footer'
import Swiper from './components/Swiper'
import Swiper1 from './components/Swiper1'
import Swiper1Mobile from './components/Swiper1Mobile'
import Swiper2 from './components/Swiper2'
import Avatar from './components/Avatar'
// 添加过滤条件
import '@/filters/product';


import '@/styles/index.scss'
import '@/styles/globalnav.min.css'
import '@/icons/iconfont.js'
// vmei默认的一些弹出框组件
import './utils/common.js'


// 懒加载
Vue.use(VueLazyload);
// Vue.use(Element);

// 分栏
Vue.component('v-Hr', Hr)
// 导航栏组件
Vue.component('v-Header', Header)
// 底部组件
Vue.component('v-Footer', Footer)
// 轮播图组件
Vue.component('v-Swiper', Swiper)
// 轮播图组件1
Vue.component('v-Swiper1', Swiper1)
// 轮播图组件1 移动端
Vue.component('v-Swiper1Mobile', Swiper1Mobile)
// 轮播图组件2
Vue.component('v-Swiper2', Swiper2)
// 自定义头像组件
Vue.component('v-Avatar', Avatar)

Vue.prototype.$progress = Nprogress
Vue.config.productionTip = false

// eslint-disable-next-line
new Vue({
  el: '#app',
  store,
  beforeCreate () {
    this.$progress.start()
  },
  mounted () {
    window.onload = () => {
      this.$progress.done()
    }
  },
  router,
  components: { App },
  template: '<App/>'
})



