import Vue from 'vue'
import Router from 'vue-router'


import NotFound from '@/views/404'
import Index from '@/views/Index.vue'
// 登录
import Login from '@/views/login'
// 商家入驻
import Register from '@/views/register'
import RegisterMobile from '@/views/register.mobile.vue'
// import JoinRequest from '@/views/joinRequest'
// 修改密码
import modifyPW from '@/views/modifyPW'
import modifyPW1 from '@/views/modifyPW/step1'
import modifyPW2 from '@/views/modifyPW/step2'
import modifyPW3 from '@/views/modifyPW/step3'
import modifyPW4 from '@/views/modifyPW/step4'
// 商家入驻要求
import CustmerJoinRequest from '@/views/CustmerJoinRequest'
// 达人开店
import FreeTry from '@/views/freeTry'

import News from '@/views/news'
import NewsList from '@/views/news/list'
import NewsDetail from '@/views/news/detail'
// import TalentJoin from '@/views/talentJoin'






// // 404
// const NotFound = () => import('@/views/404');
// // 登录
// const Login = () => import('@/views/login');
// // 商家入驻
// const Register = () => import('@/views/register');
// const RegisterMobile = () => import('@/views/register.mobile');
// // import JoinRequest from '@/views/joinRequest'
// // 修改密码
// const modifyPW = () => import('@/views/modifyPW');
// const modifyPW1 = () => import('@/views/modifyPW/step1');
// const modifyPW2 = () => import('@/views/modifyPW/step2');
// const modifyPW3 = () => import('@/views/modifyPW/step3');
// const modifyPW4 = () => import('@/views/modifyPW/step4');
// // 首页
// const Index = () => import('@/views/index');
// // 商家入驻要求
// const CustmerJoinRequest = () => import('@/views/CustmerJoinRequest');
// // 达人开店
// const FreeTry = () => import('@/views/freeTry');
// const TalentJoin = () => import('@/views/talentJoin');

Vue.use(Router)

const isMobile = screen.availWidth < 768;

const router = new Router({
  routes: [{
    path: '/',
    component: Index,
    name: '管理首页',
    hidden: true
  }, {
    path: '/login',
    component: Login,
    name: '登录页面',
    hidden: true
  }, {
    path: '/register',
    component: isMobile ? RegisterMobile : Register,
    name: '注册页面',
    hidden: true
  }, {
    path: '/custmerJoinRequest',
    component: CustmerJoinRequest,
    name: '唯美招商入驻要求',
    hidden: true
  }, {
    path: '/modifypw',
    component: modifyPW,
    name: '修改密码',
    hidden: true,
    children: [{
      path: '1',
      component: modifyPW1
    }, {
      path: `2`,
      component: modifyPW2
    }, {
      path: `3`,
      component: modifyPW3
    }, {
      path: `4`,
      component: modifyPW4
    }]
  }, {
    path: '/freetry',
    component: FreeTry,
    hidden: true
  }, {
    path: '/news',
    component: News,
    hidden: true,
    children: [
      {
        path: '/news',
        component: NewsList,
        hidden: true
      },
      {
        path: '/news/:id',
        component: NewsDetail,
        hidden: true
      }]
  }, {
    path: '*',
    component: NotFound,
    name: '未找到',
    hidden: true
  }],
  mode: 'history',
  linkActiveClass: 'active',
  linkExactActiveClass: 'active'
})

const limitRouterFunc = ({ to, next, limitPage }) => {
  if (limitPage.indexOf(to.path) >= 0) {
    next()
  } else {
    console.log('然后你要登录呢？')
    next({ path: limitPage[0] })
  }
}

router.beforeEach((to, from, next) => {
  // const isLogin = store.state.user.name.length > 0
  const isLogin = true
  if (!isLogin) {
    limitRouterFunc({
      to,
      next,
      limitPage: [`/login`, `/register`, '/', '/detail', '/custmerJoinRequest']
    })
  } else {
    next()
  }
})

export default router



