      }
      .btn-color {
        margin-top: 33px;
        background: linear-gradient(to left, #eb6140, #e2335b);
        border-radius: 100px;
        color: #fff;
        display: inline-flex;
        width: 150px;
        user-select: none;
        height: 53px;
        font-size: 20px;
        justify-content: center;
        cursor: pointer;
        align-items: center;
      }
    }
  }

  .section-three {
    width: 100%;
    background-color: #fafafa;
    padding: 30px 0;

    .container {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: flex-end;
      margin: auto;
      max-width: 1200px;

      .eng-banner {
        color: #ddd;
        display: inline-block;
        font-size: 42px;
        letter-spacing: 3px;
        font-weight: bold;
      }
      .zh-banner {
        font-size: 35px;
        letter-spacing: 0.5em;
        margin-top: 5px;
        display: inline-block;
        letter-spacing: 8px;
        font-size: 40px;
        font-weight: 500;
      }

      p.content {
        line-height: 1.8em;
        font-size: 18px;
        letter-spacing: 1px;
        margin: 22px 0;
        font-weight: 500;
      }

      .introduction-container {
        margin-top: 10px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;

        .list {
          flex: 0 0 44%;
          margin: 10px 0 50px;
          height: 360px;
          display: flex;
          align-items: flex-end;
          background-size: contain;
          justify-content: flex-start;
          background-repeat: no-repeat;
          background-position: center 0%;
          // cursor: pointer;


          &:nth-child(2),
          &:nth-child(4) {
            justify-content: flex-end;
          }
          .context {
            background: #fff;
            width: 100%;
            padding: 20px 40px;
            height: 100%;
            max-height: 180px;
            transition: 0.3s all ease-in-out;
            overflow: hidden;
            .context-title {
              color: #e33858;
              font-size: 26px;
              letter-spacing: 3px;
              margin: 20px 0;
              font-weight: bold;
            }
            .context-content {
              min-height: 42px;
              letter-spacing: 2px;
            }
            p + p {
              margin-top: 30px;
            }
          }
        }
      }
    }
  }

  .section-four {
    position: relative;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto 100px;
    height: 660px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    text-align: center;

    .eng-banner {
      color: #ddd;
      display: inline-block;
      font-weight: 600;
      font-size: 40px;
      letter-spacing: 5px;
    }
    .zh-banner {
      margin-top: 20px;
      letter-spacing: 0.5em;
      display: inline-block;
      letter-spacing: 8px;
      font-size: 35px;
    }

    .advance-container {
      display: flex;
      flex-direction: row;
      width: 100%;
      flex-wrap: wrap;
      justify-content: space-between;

      .list {
        width: 500px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        text-align: justify;
        margin: 60px 0 0;

        .title {
          width: 100%;
          color: #e12c5f;
          margin: 30px 0;
          display: inline-flex;
          font-size: 35px;
          text-align: left;
          align-items: center;
          letter-spacing: 2px;
          justify-content: flex-start;

          .icon {
            flex: 0 0 50px;
            font-size: 38px;
            margin-right: 5px;
          }
        }
        .content {
          text-align: left;
          line-height: 1.8em;
          font-size: 18px;
          letter-spacing: 1px;
          font-weight: 500;
        }
      }
    }
  }

  .section-five {
    position: relative;
    margin: 70px 0 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    .eng-banner {
      color: #ddd;
      display: inline-block;
      font-weight: 600;
      font-size: 40px;
      letter-spacing: 5px;
    }
    .zh-banner {
      margin-top: 20px;
      letter-spacing: 0.5em;
      display: inline-block;
      letter-spacing: 8px;
      font-size: 40px;
    }

    .contaienr {
      max-width: 1200px;
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-top: 100px;

      .left {
        flex: 0 0 44%;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        background-size: contain;
        background-position: center;
        filter: brightness(0.9) contrast(90%);
        transition: 0.1s all ease-in-out;
        .btn-start {
          cursor: pointer;
          display: inline-flex;
          color: #fff;
          background: rgba(0, 0, 0, 0.8);
          display: inline-flex;
          align-items: center;
          justify-content: center;
          width: 130px;
          height: 45px;
          font-size: 17px;
          border-radius: 100px;

          svg.icon {
            margin-right: 10px;
            font-size: 25px;
          }
        }
        video {
          width: 100%;
          height: 100%;
        }
        &:hover {
          filter: none;
        }
      }
      .right {
        flex: 0 0 44%;
        text-align: left;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .title {
          color: #e33858;
          font-size: 35px;
          font-weight: 500;
        }
        .main-content {
          font-size: 24px;
          margin-bottom: 25px;
          margin-top: 5px;
          letter-spacing: 2px;
        }
        .content {
          margin-bottom: 5px;
        }
        .btn-group {
          width: 100%;
          margin-top: 50px;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          a {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            width: 150px;
            height: 50px;
            font-size: 16px;
            border-radius: 5px;
            background: #fff;
            border: none;
            cursor: pointer;
          }
          a:nth-child(1) {
            color: #fff;
            background: #e7567e;
          }
          a:nth-child(2) {
            border: 2px solid #e7567e;
            color: #e7567e;
          }
        }
      }
    }
  }

  .section-six {
    width: 100%;
    // background-color: #f5f3f2;
    padding: 30px 0;

    .container {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: flex-end;
      margin: auto;
      max-width: 1200px;
      border-bottom: 2px solid #dfdfdf;

      .zh-banner {
        font-size: 35px;
        margin-top: 5px;
        display: inline-block;
        font-size: 40px;
        font-weight: 500;
      }

      .introduction-container {
        margin-top: 10px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;

        .list {
          padding: 16px;
          flex: 0 0 200px;
          margin: 30px 0;
          background: #fff;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          align-items: center;
          height: 215px;

          img {
            max-width: 110px;
            max-height: 110px;
            width: 110px;
            height: 110px;
            margin: 20px 33px 8px;
          }

          .collaboration {
            color: #e33858;
            font-size: 120px;
            transform: scale(1.2);
            transform-origin: 50% 18%;
          }

          .edit {
            color: #e33858;
            margin: 20px 33px 8px;
            font-size: 100px;
          }

          .context-title {
            font-size: 16px;
          }

          .context-content {
            font-size: 12px;
          }
        }
      }
    }
  }

  .section-seven {
    width: 100%;
    background-color: #fff;
    padding: 60px 0;

    .container {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: flex-end;
      margin: auto;
      max-width: 1200px;

      .zh-banner {
        font-size: 35px;
        display: inline-block;
        font-weight: 500;
        color: #ee2c47;
        font-weight: bold;
      }

      .low-banner {
        font-size: 20px;
        margin: 10px;
        letter-spacing: 2px;
      }

      .introduction-container {
        margin-top: 20px;
        display: flex;
        flex-direction: row;
        justify-content: center;

        .list {
          flex: 0 0 140px;
          background: #fff;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          align-items: center;

          .icon {
            color: #e33858;
            font-size: 50px;
          }

          .context-title {
            font-size: 16px;
            font-weight: bold;
            margin: 10px 0;
            letter-spacing: 2px;
          }

          .context-content {
            font-size: 12px;
            letter-spacing: 2px;
          }
        }
      }
    }
  }
}

.vedio-container {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #333;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,.8);
  z-index: 300;
  display: flex;
  align-items: center;
  justify-content: center;

  video {
    width: 880px;
    height: 495px;
    z-index: 301;
  }
  .close-btn {
    position: absolute;
    width: 38px;
    color: #000;
    z-index: 302;
    height: 38px;
    padding: 10px;
    display: flex;
    cursor: pointer;
    font-size: 35px;
    background: #fff;
    border-radius: 100px;
    top: calc(50% - 266px);
    left: calc(50% + 421px);
    align-items: center;
    justify-content: center;
  }
  @media (max-width: 600px) {
    video {
      width: 100%;
      height: 65vw;
    }
  }
}

@media (max-width: 768px) {
  .el-container {
    display: flex;
    flex-direction: column;
    .section-one {
      height: calc(100vh - 222px);
      background-position-x: 75%;
      padding: 0;

      .outer-container.swiper1 {
        background-image: url(https://assets.vmei.com/system/cosmos/images/newCosmos/background-mobile.png);
        background-position: center bottom;

        .inner-container {
          max-width: 1200px;
          width: 100%;
          height: 100%;
          padding: 65px 0;
          margin: auto;
          background: rgba(0,0,0,0.3);
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;

          .zh-banner {
            font-size: 22px;
            letter-spacing: 3px;
            margin: 5px 0;
          }
          .eng-banner {
            letter-spacing: 1px;
            font-size: 16px;
            margin: 0;
          }

          .btns {
            display: none;
          }
          .btns-mobile {
            display: flex;
            flex-direction: row;
            justify-content: center;
            .btn-color + .btn-color {
              margin-left: 70px;
            }
            .btn-color {
              margin-top: 80px;
              border-radius: 5px;
              color: #fff;
              display: inline-flex;
              width: 100px;
              user-select: none;
              height: 40px;
              font-size: 15px;
              cursor: pointer;
              align-items: center;
              justify-content: center;
              background: linear-gradient(to left, #eb6140, #e2335b);
            }
          }
        }
      }
    }
    .section-two {
      display: none;
      // flex-direction: column;
      // align-items: center;
      // margin-top: 20px;
      // padding: 0;
      // height: auto;
      // .list {
      //   text-align: center;
      //   width: 90%;
      //   .title {
      //     font-size: 24px;
      //   }
      //   .detail {
      //     font-size: 16px;
