      //     line-height: 1.4em;
      //   }
      //   .btn-color {
      //     margin: 20px 0;
      //     width: 130px;
      //     height: 40px;
      //   }
      // }
      // .br-left {
      //   display: none;
      // }
      // .list:last-child {
      //   border-left: 0;
      //   border-top: 2px solid #ccc;
      // }
    }
    .section-three {
      padding: 0;
      margin: 0;
      width: 100%;
      background: #fff;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      .container {
        margin-bottom: 20px;
        .zh-banner {
          letter-spacing: 0;
          margin: 0;
          padding: 20px 0;
          font-size: 17px;
          font-weight: 500;
          width: 100%;
          text-align: center;
        }
        .eng-banner {
          font-size: 20px;
          letter-spacing: 3px;
          margin-top: 10px;
        }
        p.content {
          padding: 0 20px;
          color: #888;
          font-size: 13px;
          margin: 0;
        }

        .introduction-container {
          display: none;
          flex-direction: column;
          font-size: 13px;
          margin: 0;
          .list {
            height: auto;
            margin: 10px 20px;
            margin-top: 0px;
            box-sizing: border-box;
            text-align: center;
            box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
            .context {
              margin-top: 105px;
              padding: 20px;
              .context-title {
                text-align: center;
                margin: 0;
                padding: 10px 0;
                font-size: 16px;
              }
              .context-content {
                margin: 0;
              }
            }
            img {
              width: 100%;
            }
          }
        }
      }
    }
    .section-four {
      order: 4;
      margin: 0 auto 0;
      width: 100%;
      padding: 0 18px 0 18px;
      height: auto;
      background: #f8f8f8;
      .zh-banner {
        letter-spacing: 0;
        margin: 0;
        padding: 20px 0;
        font-size: 17px;
        font-weight: 500;

        &::before {
          content: "-";
          padding: 0 5px;
        }
        &::after {
          content: "-";
          padding: 0 5px;
        }
      }
      .eng-banner {
        font-size: 17px;
        display: none;
        letter-spacing: 0px;
      }
      .advance-container {
        flex-direction: column;
        flex-wrap: nowrap;
        margin: 0;
        .list {
          background: #fff;
          margin: 0;
          text-align: justify;
          width: 100%;
          height: auto;
          padding: 20px;
          padding-top: 5px;
          border: 1px solid #f2f2f2;
          margin-bottom: 10px;
          flex-direction: column;
          .title {
            width: 100%;
            color: #000;
            font-size: 16px;
            margin: 0 0 10px 0;
            justify-content: center;
            flex-direction: column;
            font-weight: 500;
            svg.icon {
              color: #e2335b;
              margin: 10px;
              font-size: 40px;
              flex: 0 0 auto;
            }
          }
          .content {
            line-height: 22px;
            margin: 0;
            text-align: center;
            box-sizing: border-box;
            width: 100%;
            font-size: 13px;
            color: #888;
            line-height: 22px;
          }
          img {
            width: 100%;
          }
        }
      }

      .frame-png {
        left: calc(50vw - 429px);
        @media (max-width: 600px) {
          overflow: hidden;
        }
      }
    }
    .section-five {
      order: 5;
      margin: 0 auto 0;
      width: 100%;
      padding: 0 18px 20px 18px;
      height: auto;
      background: #f8f8f8;
      .eng-banner.mobile {
        font-size: 20px;
        display: none;
      }
      .zh-banner {
        letter-spacing: 0;
        margin: 0;
        padding: 10px 0 20px;
        font-size: 17px;
        font-weight: 500;

        &::before {
          content: "-";
          padding: 0 5px;
        }
        &::after {
          content: "-";
          padding: 0 5px;
        }
      }
      .contaienr {
        margin-top: 0;
        flex-direction: column;
        background: #fff;
        border: 1px solid #f2f2f2;
        .left {
          height: 64vw;
          margin: 10px 20px;
          background-size: cover;
          display: none;
        }
        .right {
          margin: 20px;
          text-align: center;
          align-items: center;
          .icon {
            color: #e12c5f;
            font-size: 40px;
            margin:  0 10px 10px;
          }
          .title {
            font-size: 20px;
            width: 100%;
            color: #000;
            font-size: 16px;
            margin: 0 0 10px 0;
          }
          .main-content {
            font-size: 17px;
            display: none;
          }
          .btn-group {
            text-align: center;
            justify-content: center;
            margin-top: 20px;
          }
          .content {
            line-height: 22px;
            margin: 0;
            text-align: center;
            box-sizing: border-box;
            width: 100%;
            font-size: 13px;
            color: #888;
            line-height: 22px;
          }
          .btn-group {
            a {
              width: 100px;
              height: 30px;
              font-size: 15px;
            }
          }
        }
      }
    }
    .section-six {
      order: 6;
      background: #fff;
      padding: 0;
      padding-bottom: 20px;
      .container {
        width: 100%;

        .zh-banner {
          font-size: 20px;
          margin-top: 20px;
          font-weight: bold;
          font-size: 16px;
          letter-spacing: 1px;
        }
        .br {
          width: 100%;
          letter-spacing: 3px;
          font-size: 12px;
          display: flex;
          align-items: center;
          justify-content: center;
          position: relative;
          color: #888;
          &::after {
            content: ' ';
            position: absolute;
            display: inline-block;
            width: 50%;
            border-bottom: 1px solid #efefef;
            left: -70px;
            top: 8px;
          }
          &::before {
            content: ' ';
            position: absolute;
            display: inline-block;
            width: 50vw;
            border-bottom: 1px solid #efefef;
            right: -70px;
            top: 8px;
          }
        }
        img {
          width: 30vw;
          height: 30vw;
          margin: 25px auto 5px;
        }
      }
    }
    .footer-mobile {
      background-color: #333;
      padding: 20px 20px 30px;
      color: #fff;
      font-size: 12px;
      text-align: center;
      width: 100%;
      overflow: hidden;
      order: 7;
      h3 {
        font-size: 15px;
        padding: 10px 0;
        font-weight: 500;
      }
      p {
        line-height: 24px;
        font-weight: 300;
        font-size: 10px;
      }
      img {
        width: 30vw;
        height: 30vw;
        margin: 10px 10px 20px;
      }
    }
  }
}
</style>



