<template>
  <nav :class="bk">
    <div class="container">
      <a href="https://www.vmei.com/">
        <img src="@/assets/logo6.png" alt="logo">
      </a>

      <ul>
        <li data-href="/" class="list four">
          <a href="https://www.vmei.com/">
            {{$store.state.lang === 'zh'? '首页': 'Home'}}
          </a>
        </li>

        <li data-href="/register" class="list five">
          <a href="https://business.vmei.com/register">
            {{$store.state.lang === 'zh'? '商户入驻': 'Brand Join'}}
          </a>
        </li>

        <li data-href="/freetry" class="list four">
          <a href="https://business.vmei.com/freetry">
            {{$store.state.lang === 'zh'? '达人开店': 'Talent Join'}}
          </a>
        </li>

        <li class="list four">
          <a href="https://www.vmei.com/mall.html">
            {{$store.state.lang === 'zh'? '唯美商城': 'Vmei Mall'}}
          </a>
        </li>

        <li class="list four">
          <router-link tag="a" to="/news">
            新闻中心
          </router-link>
        </li>

        <li data-href="/login" class="list four">
          <a href="https://business.vmei.com/newCosmos/index/#/login">
            {{$store.state.lang === 'zh'? '商户登录': 'Login'}}
          </a>
        </li>

      </ul>
    </div>
    <div class="mobile">

      <a href="https://www.vmei.com/" v-if="banner">
        <img src="https://assets.vmei.com/system/cosmos/images/newCosmos/logo3.png" alt="logo">
      </a>
      <a class="login-btn" href="https://business.vmei.com/newCosmos/index/#/login">
        {{$store.state.lang === 'zh'? '登录': 'Login'}}
      </a>
    </div>
  </nav>
</template>

<style lang="scss" scoped>
nav {
  z-index: 2;
  width: 100%;
  color: #fff;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);

  .mobile {
    display: none;
  }
  .toggle-mobile {
    display: none;
  }

  .container {
    overflow: hidden;
    height: 71px;
    align-items: center;
    // padding: 0 0 0 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    width: 100%;
    font-weight: 500;
    margin: auto;

    img {
      height: 45px;
    }

    ul {
      display: inline-flex;
      flex-direction: row;
      list-style-type: none;
      height: 65px;
      align-items: center;

      li.list {
        cursor: pointer;
        font-size: 14px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        color: #000;
        position: relative;
        &::after {
          transition: 0.1s all ease-in-out;
          bottom: -1px;
          content: ' ';
          position: absolute;
          width: calc(100% - 40px);
          height: 0;
          display: inline-block;
          background-color: #d84132;
        }
        a{
          padding: 25px;
        }
      }
      li:hover /*, li.active */ {
        &::after {
          height: 4px;
        }
      }
    }
  }
}

@media (max-width: 768px) {
  nav.white {
    background-color: rgba(0, 0, 0, 0);
  }
  nav {
    position: absolute;
    box-shadow: none;

    .container {
      display: none;
    }
    .mobile {
      z-index: 1;
      height: 80px;
      padding: 0 15px;
      position: relative;
      display: inline-block;
      width: 100%;

      img {
        height: 40px;
        left: 10px;
        top: 10px;
        position: absolute;
      }

      .login-btn {
        font-size: 12px;
        line-height: 18px;
        font-weight: 500;
        color: #fff;
        background-color: rgba(255,255,255,.3);
        border-radius: 5px;
        padding: 5px 20px;
        margin-bottom: 5px;
        position: absolute;
        right: 15px;
        top: 15px;
      }
    }
  }
}
.none {
  display: none;
}
</style>

<script>
import store from '@/store'
export default {
  name: 'v-Header',
  data () {
    return {
      bk: this.$attrs.bk,
      isMobile: window.screen.availWidth < 600,
      banner: this.$attrs.banner === undefined ? true : this.$attrs.banner
    }
  },
  mounted () {
    window.app = this
    window.store = store;
    const currentActiveNav = document.querySelector(`li[data-href="${location.pathname}"]`);
    if (currentActiveNav) {
      currentActiveNav.classList.add('active');
    }
  },
  methods: {
    changeLanguage (e) {
      if (e.target.dataset.lang) {
        store.commit('syncStateStr', {
          name: 'lang',
          val: e.target.dataset.lang
        })
        localStorage.setItem('language', e.target.dataset.lang)
      }
    }
  }
}
</script>



