<template>
  <div class="el-pagination is-background">
    <button type="button" :disabled="page==1" @click="prev" class="btn-prev">
      &lt;
    </button>
    <ul class="el-pager">
      <li @click="changePage(i+1)" v-for="(item,i) in list" :class="`number ${page===i+1 ? 'active' : ''}`" :key="i">{{i+1}}</li>
    </ul>
    <button type="button" :disabled="total/pageSize == page" class="btn-next" @click="next">
      &gt;
    </button>
    <!-- <span class="el-pagination__jump">
      前往
      <div class="el-input el-pagination__editor is-in-pagination">
        <input type="number" autocomplete="off" min="1" max="6" class="el-input__inner">
      </div>
      页
    </span>
    <span class="el-pagination__total">共 275 条</span> -->
  </div>
</template>

<script>
export default {
  name: 'elPagination',
  data () {
    return {
      total: this.$attrs.total,
      pageSize: this.$attrs.pageSize,
      page: this.$attrs.page // 当前页数
    }
  },
  computed: {
    list () {
      const size = Math.floor(this.total / this.pageSize);
      const arr = new Array(size).fill(1);
      return arr;
    }
  },
  methods: {
    changePage (i) {
      this.page = i;
      this.$emit('changePage', i)
    },
    prev () {
      this.$emit('changePage', this.page - 1)
      this.page--;
    },
    next () {
      this.$emit('changePage', this.page + 1)
      this.page++;
    }
  }
}
</script>

<style lang="scss" scoped>
  .el-pagination {
    display: flex;
    flex-direction: row;

    button {
      display: inline-block;
      height: 30px;
      line-height: 30px;
      text-align: center;
      width: 30px;
      margin-right: 10px;
      cursor: pointer;
      background-color: #f4f4f5;
      color: #606266;
      border-radius: 2px;
      border: 0;
      outline: none;
    }
    ul {
      list-style-type: none;
      display: flex;
      flex-direction: row;
      li {
        cursor: pointer;
        display: inline-block;
        height: 30px;
        width: 30px;
        line-height: 30px;
        text-align: center;
        margin-right: 10px;
        background-color: #f4f4f5;
        color: #606266;
        min-width: 30px;
        border-radius: 2px;

        &.active {
          color: #fff;
        }
      }
    }
  }
</style>



