<template>
  <div class="el-container">
    <!-- 首部banner -->
    <v-Header :banner="false"/>
    <!-- 注册框 -->
    <section v-lazy:background-image="`https://assets.vmei.com/system/cosmos/images/newCosmos/background1-mobile.png`" class="section-one">
      <div class="content">
        <p class="high-params">品质时尚&nbsp;非你莫属</p>
        <v-Hr class='hr' color="#fff"/>
        <p class="low-params">加入唯美美妆，触达中国</p>
        <p class="low-params">亿万美妆消费社群</p>
      </div>
      <img class="output" src="https://assets.vmei.com/system/cosmos/images/newCosmos/output.png" alt="">
      <div class="form-container">
        <div class="title">
          注册成为唯美商家
        </div>
        <form action="" v-on:submit.prevent="submitFunc">
          <span class="container-input">
            <input ref="companyname" maxlength="30" type="text" v-model="companyData.companyname" placeholder="公司名称">
            <input ref="companyAddress" maxlength="100" type="text" v-model="companyData.companyAddress" placeholder="公司地址">
          </span>
          <span class="container-input">
            <input ref="businessName" maxlength="30" type="text" v-model="contactsData.businessName" placeholder="联系人">
            <input ref="businessEmail" maxlength="30" type="text" v-model="contactsData.businessEmail" placeholder="邮箱">
            <input ref="businessPhone" maxlength="11" type="text" v-model="contactsData.businessPhone" placeholder="电话">
            <input ref="wechat" maxlength="30" type="text" v-model="contactsData.wechat" placeholder="微信">
          </span>
          <button class="login">
            提交申请
            <!-- <span class="right">☑</span> -->
          </button>
        </form>
      </div>
    </section>

    <!-- 流程要求 -->
    <section class="section-two">
      <div class="list-container">
        <div class="zh-banner">入驻流程</div>
        <v-Hr/>
        <span class="list">
          <img src="https://assets.vmei.com/system/cosmos/images/newCosmos/icon-pen.png" alt="logo">
          <span class="right">
            <span class="main">1.填写信息</span>
            <span class="info">约15分钟，请正确填写公司信息，填写公司名、地址和联系方式。</span>
          </span>
        </span>
        <span class="list">
          <img src="https://assets.vmei.com/system/cosmos/images/newCosmos/icon-clock.png" alt="logo">
          <span class="right">
            <span class="main">2.等待审核</span>
            <span class="info">约3-6个工作日，对您提交的公司信息和品牌信息进行初步审核审核结果通过邮箱发送。</span>
          </span>
        </span>
        <span class="list">
          <img src="https://assets.vmei.com/system/cosmos/images/newCosmos/icon-email.png" alt="logo">
          <span class="right">
            <span class="main">3.生成账号</span>
            <span class="info">约1个工作日，首次登录必须修改密码，完善公司资料，品牌信息，联系人资料等等。</span>
          </span>
        </span>
        <span class="list">
          <img src="https://assets.vmei.com/system/cosmos/images/newCosmos/icon-shop.png" alt="logo">
          <span class="right">
            <span class="main">4.完成入驻</span>
            <span class="info">约3-5个工作日，恭喜您！完成入驻，马上发布商品。</span>
          </span>
        </span>
      </div>
      <div class="join-request">
        <h3>入驻要求：</h3>
        <ul>
          <li>1、注册资本不得低于人名币50万元；</li>
          <li>2、国内企业须具备一般纳税人资格；</li>
          <li>3、品牌需提供商标注册证（即R标）；</li>
          <li>4、经营国产化妆品，化妆品生产厂商需取得《化妆品生产许可证》；</li>
          <li>5、如经营进口商品，需提供近两年内合法渠道进口证明，取得中华人民共和国进口非特殊用途/特殊用途化妆品备案凭证；跨境电商品牌需要具备跨境发货和物流作业的能力凭证；</li>
          <li>6、所经营的商品或服务必须符合法律法规、国家标准等质量要求，同时符合平台商品质量标准、规则及商品发布规范等要求；</li>
          <li>7、所有提交资料需要加盖入驻公司公章。</li>
        </ul>
        <router-link class="get-more" tag="button" to="/custmerJoinRequest">了解更多</router-link>
      </div>
    </section>

    <!-- 展示区 -->
    <section class="section-three">
      <div class="zh-banner">我们是专业的美妆平台</div>
      <v-Hr/>
      <img src="https://assets.vmei.com/system/cosmos/images/newCosmos/swiper-image.png" alt="logo">
    </section>
    <!-- 底部组件 -->
    <v-Footer></v-Footer>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'login',
  data () {
    return {
      loading: false,
      screenWidth: screen.availWidth,
      companyData: {
        companyname: '',
        companyAddress: ''
      },
      contactsData: {
        businessName: '',
        businessEmail: '',
        businessPhone: '',
        wechat: ''
      }
    }
  },
  mounted () {
    [
      ...document.querySelectorAll('input'),
      ...document.querySelectorAll('textarea')
    ].forEach(dom => {
      dom.addEventListener('blur', e => {
        const val = e.target.value;
        const reg = new RegExp(/('|")/g);
        if (val.match(reg)) {
          window.commonStart.tip(`不允许提交"'等特殊字符`, 'warning');
          e.target.value = val.replace(reg, '');
        }
      })
    })
  },
  methods: {
    submitFunc (e) {
      if (this.companyData.companyname === '') {
        window.commonStart.tip('公司名称不能为空', 'warning');
        this.$refs.companyname.focus();
        return;
      } else if (this.companyData.companyAddress === '') {
        window.commonStart.tip('公司地址不能为空', 'warning');
        this.$refs.companyAddress.focus();
        return;
      } else if (this.contactsData.businessName === '') {
        window.commonStart.tip('联系人不能为空', 'warning');
        this.$refs.businessName.focus();
        return;
      } else if (this.contactsData.businessEmail === '') {
        window.commonStart.tip('邮箱不能为空', 'warning');
        this.$refs.businessEmail.focus();
        return;
      } else if (this.contactsData.businessPhone === '') {
        window.commonStart.tip('电话不能为空', 'warning');
        this.$refs.businessPhone.focus();
        return;
      } else if (this.contactsData.wechat === '') {
        window.commonStart.tip('微信不能为空', 'warning');
        this.$refs.wechat.focus();
        return;
      };
      if (!this.contactsData.businessPhone.match(/1(2|3|4|5|6|7|8|9)\d\d{8}/)) {
        window.commonStart.tip('手机号码格式不合法', 'warning');
        this.$refs.businessPhone.focus();
        this.$refs.businessPhone.value = '';
        return;
      } else if (!this.contactsData.businessEmail.match(/\w{3,}@\w+\.com/)) {
        window.commonStart.tip('邮箱格式不合法', 'warning');
        this.$refs.businessEmail.value = '';
        this.$refs.businessEmail.focus();
        return;
      };
      axios({
        method: 'post',
        url: '/bussinessJoin/submit',
        params: {
          companyName: this.companyData.companyname,
          supplierType: 2,
          companyData: this.companyData,
          contactsData: this.contactsData
        }
      }).then(res => {
        this.loading = false;
        if (res.data.success) {
          /* eslint-disable */
          window.commonStart.tip('已经登录您的信息，商务人员将尽快与您联系洽谈。', 'success');
        } else {
          console.log(res.data)
          /* eslint-disable */
          window.commonStart.tip(res.data.msg, 'warning');
        }
      }).catch( err => {
        window.commonStart.tip(err, 'warning');
        console.log(err)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.el-container {
  width: 100%;
  background-color: #fff;
  flex-direction: column;

  .section-one {
    color: #fff;
    background: #efefef;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
    background-size: contain;
    background-position: center 0%;
    background-repeat: no-repeat;
    padding-bottom: 20px;

    .content {
      color: #fff;
      text-align: center;
      margin-top: 95px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      height: 120px;
      p.low-params {
        font-size: 14px;
        letter-spacing: 4px;
      }
      p.high-params {
        letter-spacing: 2px;
        font-size: 32px;
      }
      .hr {
        margin: 10px 0 15px;
      }
    }
    
    img.output {
      margin-top: 100px;
      z-index: 0;
      width: 100%;
    }

    .form-container {
      border-radius: 9px;
      z-index: 1;
      margin-top: -29vw;
      padding: 20px 20px 15px;
      padding-top: 0;
      width: 90%;
      flex: 0 0 auto;
      background-color: #fff;
      box-shadow: 1px 1px 100px 7px #00000036, 1px 13px 15px -13px #000000 inset;

      .title {
        height: 60px;
        font-weight: bold;
        width: 100%;
        font-size: 24px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        color: #000;
        img {
          margin-right: 20px;
        }
      }
      form {
        display: flex;
        flex-direction: column;
        color: rgb(103, 103, 103);

        .container-input {
          margin-bottom: 10px;
          border: 1px solid #e5e5e4;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: space-between;
          background: #fff;

          input + input {
            border-top: 1px solid #e5e5e4;
          }

          input {
            border: none;
            flex: 1;
            width: 100%;
            height: 100%;
            outline: none;
            padding: 14px;
            background-color: #fff !important;
          }
        }

        button {
          background: #ee4343;
          width: 100%;
          height: 45px;
          color: #fff;
          border: none;
          outline: none;
          text-align: center;
          padding: 0 20px;
          letter-spacing: 6px;
          cursor: pointer;

          .right {
            margin-left: 10px;
            font-size: 16px;
          }
        }
      }
    }
  }
  .section-two {
    background-color: #fff;
    margin: auto;
    display: flex;
    background-position: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
    width: 100%;
    max-width: 1200px;
    padding: 30px 0;

    .list-container {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      padding: 0 0 25px;

      .zh-banner {
        font-size: 18px;
        font-weight: 600;
        letter-spacing: 4px;
      }

      .hr {
        margin: 10px 0;
      }

      .list {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        padding: 0 20px;
        margin-top: 25px;
        img {
          width: 45px;
          margin: 13px;
        }
        .right {
          flex: 1 0 200px;
