        border: 1px solid #6f6f6f;
        border-radius: 5px;
        cursor: pointer;
        outline: none;
      }
    }
  }
  .section-three {
    padding: 10px 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    text-align: center;
    position: relative;

    .zh-banner {
      font-size: 18px;
      letter-spacing: 0.3em;
      color: #333;
      display: inline-block;
    }
    .hr {
      margin: 10px 0;
    }
    img {
      width: 80%;
      margin-top: 60px;
    }
  }

}



</style>



