<template>
  <span class="avatar">
    <span class="img-container">
      <img v-if="src" :src="src" alt="头像">
      <svg v-else class="icon" aria-hidden="true">
        <use xlink:href="#icon-user"></use>
      </svg>
    </span>
    <span class="content">
      <p>供应商</p>
      <p>{{name}}</p>
    </span>

    <el-dropdown>
      <el-button>
        <i class="el-icon-arrow-down el-icon--right"></i>
      </el-button>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item>修改密码</el-dropdown-item>
        <el-dropdown-item>退出登录</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </span>
</template>

<style lang="scss" scoped>
.avatar {
  user-select: none;
  top: 0;
  color: #959ba1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 12px;

  .img-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border: 1px solid #d1d1d1;
    border-radius: 100px;
    height: 30px;
    width: 30px;
    background-color: #d1d1d1;
    svg.icon {
      font-size: 22px;
    }
    margin: 10px;
  }
  .content {
    p:first-child {
      color: #333;
      font-weight: bold;
      letter-spacing: 1px;
    }
    p:last-child {
      font-size: 12px;
    }
  }

  .el-dropdown {
    cursor: pointer;
    font-size: 16px;
    button {
      padding: 12px 10px;
      border: none;
      i {
        margin: 0;
      }
    }
  }
}
</style>

<script>
export default {
  name: 'v-Avatar',
  data () {
    return {
      name: this.$attrs.name ? this.$attrs.name : '未知',
      src: this.$attrs.src
    }
  }
}
</script>



